import React from 'react';
import styled from 'styled-components';
import {Link} from 'gatsby';
import close from '../../images/icons/close.svg';
import cookie from '../../images/icons/cookie.svg';
const BubbleContainer = styled.div`
    position: absolute;
    min-height: 15vh;
    height: auto;
    padding: 10px;
    width: 30vw;
    background: #2892F4;
    border-radius: 10px;
    grid-column: 2/6;
    margin-bottom: 5vh;
    transition-duration: 1s;
    bottom: 2vh;
    left: 2vw;
    opacity: 1;

    &::after{
        content: '';
        width: 7vw;
        height: 5vw;
        background: #2892F4;
        position: absolute;
        bottom: -2vw;
        left: 0px;
        -webkit-clip-path: polygon(0 0, 0% 100%, 45% 0);
        clip-path: polygon(0 0, 0% 100%, 45% 0);
        z-index: -9999;
    }
    & .closeIcon{
        position: absolute;
        top: 10px;
        right: 10px;
        width: 20px;
    }
    & .cookieIcon{
        height: 15vh;
        z-index: 9999999;
    }
    & .closeIcon:hover{
        transform: scale(1.25);
        cursor: pointer;
    }
    & .wrapper{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 70%;
        height: 100%;
        position: absolute;
        right: 0px;
        flex-direction: column;
    }
`
const ModalBubbleHeader = styled.p`
    font-size: 2em;
    font-weight: 900;
    text-align: left;
    color: white;
    margin: 10px;
`
const ModalBubbleContent = styled.p`
    text-align: left;
    font-size: 1.5em;
    font-weight: 900;
    line-height: 1.5;
    color: white;
`
const ModalBubble = () => {
    const closeModal = () =>{
        document.querySelector('#bubbleModalContainer').style.opacity = "0";
        window.setTimeout(() => document.querySelector('#bubbleModalContainer').style.display = "none",1000)
    }

    return(
        <BubbleContainer id="bubbleModalContainer">
            <div className="wrapper">
                <ModalBubbleHeader>Hej ty, lubisz może ciasteczka?</ModalBubbleHeader>
                <ModalBubbleContent>Bo ta strona tak, i to bardzo! Wykorzystuje je głównie do zbierania informacji o ruchu użytkowników, a korzystając z niej zgadzasz się na to. Więcej o cookies na mojej stronie przeczytasz tu: <Link to="/cookies">  link do polityki cookies</Link></ModalBubbleContent>
            </div>
            <img alt="close modal icon" src={close} className="closeIcon" onClick={closeModal}/>
            <img alt="cookie icon" src={cookie} className="cookieIcon"/>
        </BubbleContainer>
    )
}

export default ModalBubble;
